import { FormControl } from '@angular/forms';

export class HomeViewModel {
  public total: number;
  public haveToSendMoney: boolean;
  public amountControl: FormControl;
  public hasPockets: boolean;

  public validateTotal(total: number) {
    this.total = total;
    this.amountControl = new FormControl(this.total);
    this.haveToSendMoney = this.total !== 0;
  }
}
