import { Injectable } from '@angular/core';
import { CacheInteractor } from '@nequi/cache/core';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { TranslateProvider } from '@nequi/features/commons';
import { PluginDeviceProvisioningRepository } from '@nequi/features/commons/core';
import { AlertService } from '@nequi/features/commons/presentation';
import { NavigatorProvider, UserInfoProvider } from '@nequi/features/commons/providers';
import { ContactEntity, ContactsInteractor } from '@nequi/features/contacts/core';
import {
  AccountInteractor,
  AccountWithBalanceException,
  AccountWithCreditException,
  ComissionPendingForNonUse,
  SendMoneyAccountBlockedException,
  SendMoneyTargetAccountBlocked,
  SomethingPendingException
} from '@nequi/features/profile/core';
import { PinChallengeModalService } from '@nequi/features/security/auth/presentation/services';
import { CardModalService, LoadingService, ToastService } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { HomeConfig } from '../pages/home/home.config';

@Injectable()
export class CloseAccountService {
  private factoryException = {
    [AccountWithBalanceException.name]: (error) =>
      this.showPendingBalanceAlert(error.data.interestAmount),
    [AccountWithCreditException.name]: () => this.showPendingCreditAlert(),
    [SomethingPendingException.name]: () => this.showCanNotCloseAlert(),
    [ComissionPendingForNonUse.name]: () => this.showClosingCyclesCardModal(),
    [SendMoneyAccountBlockedException.name]: (error) => this.showInvalidAccountAlert(error.message),
    [SendMoneyTargetAccountBlocked.name]: () => this.showTargetAccountBlockedToast(),
  };

  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private pluginDeviceProvisioningRepository: PluginDeviceProvisioningRepository,
    private userInfoProvider: UserInfoProvider,
    private contactsInteractor: ContactsInteractor,
    private accountInteractor: AccountInteractor,
    private cacheInteractor: CacheInteractor,
    private toastService: ToastService,
    private alertService: AlertService,
    private cardModalService: CardModalService,
    private applicationDataInteractor: ApplicationDataInteractor,
    private loadingService: LoadingService,
    public pinChallengeModalService: PinChallengeModalService
  ) {}

  public async closeAccount(phoneNumberDestination = '') {
    try {
      await this.loadingService.show();

      const softToken = await this.pluginDeviceProvisioningRepository.getSoftToken();

      await this.accountInteractor.close({
        softToken,
        phoneNumberDestination,
        reasonCode: '',
      });

      await this.loadingService.hide(true);
      this.processSuccess();
    } catch (error) {
      await this.loadingService.hide(false);
      this.processFail(error);
    }
  }

  public async showLooseEndsCardModal() {
    const modal = this.translateProvider.get(HomeConfig.i18n.modals.looseEnds);

    await this.cardModalService.show({
      imgPath: modal.image,
      title: modal.title,
      content: modal.content,
      buttons: [
        {
          text: modal.button,
        },
      ],
    });
  }

  public async validateContact(contact: ContactEntity, changeContactCallback: () => void) {
    if (this.userInfoProvider.getPhoneNumber() === contact.phoneNumber) {
      await this.showMirrorCardModal();

      return;
    }

    if (!(await this.contactsInteractor.isNequiUser(contact))) {
      await this.showInvalidContactAlert(changeContactCallback);

      return;
    }

    await this.showTimeHasComeAlert(contact);
  }

  public async showCloseAccountAlert() {
    await this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.alerts.closeAccount,
      handlers: {
        accept: async () => {
          const isValid = await this.validatePin();

          if (isValid) {
            await this.closeAccount();
          }
        },
      },
    });
  }

  private async showTimeHasComeAlert(contact: ContactEntity) {
    const alert = this.translateProvider.get(HomeConfig.i18n.alerts.timeHasCome);

    await this.alertService.show({
      messages: {
        header: alert.header,
        message: createFormat(alert.message, [contact.firstName || contact.phoneNumber]),
        buttons: alert.buttons,
      },
      handlers: {
        accept: async () => {
          const isValid = await this.validatePin();

          if (isValid) {
            await this.closeAccount(contact.phoneNumber);
          }
        },
        cancel: () => this.navigatorProvider.back(),
      },
    });
  }

  private async showMirrorCardModal() {
    const modal = this.translateProvider.get(HomeConfig.i18n.modals.mirror);

    await this.cardModalService.show({
      imgPath: modal.image,
      title: modal.title,
      content: modal.content,
      buttons: [
        {
          text: modal.button,
        },
      ],
    });
  }

  private async showGoodbyeCardModal() {
    const modal = this.translateProvider.get(HomeConfig.i18n.modals.goodbye);

    await this.cardModalService.show({
      imgPath: modal.image,
      title: modal.title,
      content: modal.content,
      buttons: [
        {
          text: modal.button,
        },
      ],
    });
  }

  private async showClosingCyclesCardModal() {
    const modal = this.translateProvider.get(HomeConfig.i18n.modals.closingCycles);

    await this.cardModalService.show({
      imgPath: modal.image,
      title: modal.title,
      content: modal.content,
      buttons: [
        {
          text: modal.buttons.recharge,
          handler: async () => {
            await this.navigatorProvider.navigateRoot(HomeConfig.routes.backpack);
            await this.navigatorProvider.navigateForward(HomeConfig.routes.cashIn);
          },
        },
        {
          text: modal.buttons.quit,
          color: 'medianoche',
        },
      ],
    });
  }

  private async showInvalidContactAlert(changeContactCallback: () => void) {
    await this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.alerts.invalidContact,
      handlers: {
        accept: changeContactCallback,
      },
    });
  }

  private async showInvalidAccountAlert(message: string) {
    const alert = this.translateProvider.get(HomeConfig.i18n.alerts.invalidAccount);

    await this.alertService.show({
      messages: {
        header: alert.header,
        message: message || alert.message,
        buttons: alert.buttons,
      },
      handlers: {
        accept: () => this.navigatorProvider.back(),
      },
    });
  }

  private async showPendingBalanceAlert(interestAmount: string) {
    const alert = this.translateProvider.get(HomeConfig.i18n.alerts.pendingBalance);

    await this.alertService.show({
      messages: {
        header: alert.header,
        message: alert.message,
        buttons: alert.buttons,
      },
      handlers: {
        accept: () =>
          this.navigatorProvider.navigateForward(HomeConfig.routes.sendMoney, {
            state: { total: interestAmount },
          }),
      },
    });
  }

  private async showTargetAccountBlockedToast() {
    const message = this.translateProvider.get(HomeConfig.i18n.toast.targetAccountBlocked);

    await this.toastService.showError(message);
  }

  private async showCanNotCloseAlert() {
    await this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.alerts.canNotClose,
      handlers: {
        accept: () => openUrl(HomeConfig.routes.helpForm),
        cancel: () => this.navigatorProvider.navigateRoot(HomeConfig.routes.backpack),
      },
    });
  }

  private async showPendingCreditAlert() {
    await this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.alerts.pendingCredit,
    });
  }

  private async processSuccess() {
    try {
      await this.pluginDeviceProvisioningRepository.deleteAccounts();
      await this.deleteData();
      await this.showGoodbyeCardModal();
      await this.navigatorProvider.navigateRoot(HomeConfig.routes.signIn);
    } catch (error) {
      await this.processError();
    }
  }

  private async processFail(error) {
    if (this.factoryException[error.constructor.name]) {
      this.factoryException[error.constructor.name](error);

      return;
    }

    await this.toastService.showError(error.message);
  }

  private async processError() {
    try {
      await this.pluginDeviceProvisioningRepository.deleteAccounts();
      await this.deleteData();
      await this.navigatorProvider.navigateRoot(HomeConfig.routes.signIn);
    } catch (error) {
      this.showInvalidAccountAlert(null);
    }
  }

  private async deleteData() {
    await this.applicationDataInteractor.deleteAll();
    await this.cacheInteractor.clearAll();
  }

  private async validatePin() {
    const pinChallenge = this.translateProvider.get(HomeConfig.i18n.pinChallenge);

    return this.pinChallengeModalService.show({
      title: pinChallenge.title,
      message: pinChallenge.message,
    });
  }
}
