import { CurrencyPipe } from '@angular/common';
import { Component, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { CurrencyConfigProvider } from '@nequi/ui';

import { CloseAccountService } from '../../services/close-account.service';
import { SendMoneyConfig } from './send-money.config';
import { SendMoneyViewModel } from './send-money.view-model';

@Component({
  selector: 'app-close-account-send-money',
  templateUrl: './send-money.page.html',
  styleUrls: ['./send-money.page.scss'],
})
export class SendMoneyPage extends FeatureBasePage {
  public viewModel = new SendMoneyViewModel();
  public config = SendMoneyConfig;

  public get decimalLimit() {
    return this.currencyConfigProvider.visualDecimalLimit;
  }

  constructor(
    injector: Injector,
    private router: Router,
    private closeAccountService: CloseAccountService,
    private currencyConfigProvider: CurrencyConfigProvider,
    private currencyPipe: CurrencyPipe
  ) {
    super(injector);
    const total = this.router.getCurrentNavigation().extras.state.total;

    this.viewModel.createFormGroup();
    this.viewModel.amount.setValue(this.currencyPipe.transform(total));
  }

  public validatePhoneNumberAndCloseAccount() {
    this.closeAccountService.validateContact(this.viewModel.contact.value, () =>
      this.cleanContact()
    );
  }

  private cleanContact() {
    this.viewModel.contact.setValue(null);
  }
}
