import { Component, Injector, OnInit } from '@angular/core';
import { TranslateProvider } from '@nequi/features/commons';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  BalanceInteractor,
  MoneyManagementInteractor,
  Pocket,
  PocketType
} from '@nequi/features/money-management/core';
import { CurrencyConfigProvider } from '@nequi/ui';

import { CloseAccountService } from '../../services/close-account.service';
import { HomeConfig } from './home.config';
import { HomeViewModel } from './home.view-model';

@Component({
  selector: 'app-close-account-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage extends FeatureBasePage implements OnInit {
  public config = HomeConfig;
  public viewModel = new HomeViewModel();
  public showBusyIndicator: boolean;
  public busyIndicatorError: string;

  public get decimalLimit() {
    return this.currencyConfigProvider.visualDecimalLimit;
  }

  constructor(
    injector: Injector,
    private balanceInteractor: BalanceInteractor,
    private moneyManagementInteractor: MoneyManagementInteractor,
    private closeAccountService: CloseAccountService,
    private currencyConfigProvider: CurrencyConfigProvider,
    private translateProvider: TranslateProvider
  ) {
    super(injector);
  }

  public async ngOnInit() {
    await this.getBalance();
  }

  public closeAccount() {
    this.closeAccountService.showCloseAccountAlert();
  }

  public async goToSendMoney() {
    if (this.viewModel.hasPockets) {
      await this.closeAccountService.showLooseEndsCardModal();

      return;
    }

    this.navigatorProvider.navigateForward([this.config.routes.sendMoney], {
      state: { total: this.viewModel.total },
    });
  }

  public async getBalance() {
    try {
      this.showBusyIndicator = true;
      this.busyIndicatorError = '';

      const { total } = await this.balanceInteractor.getBalance();

      this.viewModel.validateTotal(total);

      if (total > 0) {
        await this.getAllPockets();
      }

      this.showBusyIndicator = false;
    } catch (error) {
      this.busyIndicatorError = this.translateProvider.get(this.config.i18n.errorMessage);
    }
  }

  private async getAllPockets() {
    const pockets = await this.moneyManagementInteractor.getPockets([
      PocketType.AVAILABLE,
      PocketType.POCKET,
      PocketType.GOAL,
      PocketType.MATTRESS,
      PocketType.CARD,
    ]);

    this.processPockets(pockets);
  }

  private processPockets(pockets: Pocket[]) {
    const types = [PocketType.POCKET, PocketType.GOAL, PocketType.MATTRESS];

    for (const pocket of pockets) {
      if (types.includes(pocket.type) && pocket.amount > 0) {
        this.viewModel.hasPockets = true;

        return;
      }
    }
  }
}
